.tab {
        position: relative;
        padding-top: 40px;
        overflow: hidden;
        font-size: 14px;
    }
    
    .tab .tab-nav {
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 1;
        border-bottom: 1px solid #ccc;
        height: 30px;
    }
    
    .tab .tab-title {
        display: inline-block;
        border: 1px solid #d7dde4;
        padding: 0 1em;
        cursor: pointer;
        border-bottom: 0;
        border-radius: 4px 4px 0 0;
        transition: all .3s ease-in-out;
        background: #f5f7f9;
        margin-right: .5em;
        height: 29px;
        line-height: 29px;
    }
    
    .tab .tab-title.tab-active {
        height: 30px;
        background: #fff;
        transform: translateZ(0);
        border-color: #d7dde4;
        color: #39f;
    }
    
    .tab .tab-title.tab-disabled {
        pointer-events: none;
        cursor: default;
        color: #ccc;
    }
    
    .tab .tab-extra {
        position: absolute;
        right: 0;
        top: 0;
        z-index: 2;
    }
    
    .tab .tab-cont {
        display: none;
    }
    
    .tab .tab-cont.tab-active {
        display: block
    }